    async setVault(_vault){
       
 
        
        try{
            
            try{document.getElementById("item_standard_filter").style.display="none"}catch(e){}
            //document.getElementById("erc1155_standard_filter").style.display="none"
            try{document.getElementById("erc721_standard_filter").style.display="none"}catch(e){}
            try{document.getElementById("personalfeed_panel").innerHTML=""}catch(e){}
            let _ico=""
            if(_vault=="inventory")_ico="";
            let v=_vault
            if(_vault=="intro"){_ico=""; v="" }
            if(_vault=="visual"){_ico="";await this.loadSections(getUrlVars()["account"],_vault)}
            if(_vault=="vox"){_ico="";await this.loadSections(getUrlVars()["account"],_vault)}
            if(_vault=="audio"){_ico="";await this.loadSections(getUrlVars()["account"],_vault)}
            if(_vault=="video"){_ico="";await this.loadSections(getUrlVars()["account"],_vault)}
            this.setState({ico:v+" "+_ico})
        //document.getElementById("personal_mainlabel").textContent=v+" "+_ico
            
            if((_vault!="intro")&&(_vault!="meta"))
            document.getElementById("personal_description").style.display="none"
            if(_vault=="intro")
            document.getElementById("personal_description").style.display="block"
            if(_vault!="inventory")
            document.getElementById("personal_inventory_panel").style.display="none"
            if(_vault=="inventory")
            document.getElementById("personal_inventory_panel").style.display="block"
            }catch(e){}
        await utils.loadAllPersonalEvents("default",utils.getUrlVars()["account"],_vault,this.state.standard,document.getElementById("personalfeed_panel"))
        await this.setState({vault:_vault})
    }


    async setStandard(stn){
        await this.setState({standard:stn})
        document.getElementById("personalfeed_panel").innerHTML=""
        await utils.loadAllPersonalEvents("default",utils.getUrlVars()["account"],this.state.vault,stn,document.getElementById("personalfeed_panel"))
    }


    async componentDidMount() {
        let _folder
       try{ utils.loadShopColors(getUrlVars()["account"])
       document.getElementById("personal_name").textContent=cleanText(await utils.loadMetaParam(getUrlVars()["account"],"name"))
       let desc=await utils.loadMetaParam(getUrlVars()["account"],"description")
       desc=desc.replaceAll("<br>","\n")
       await this.setState({description:desc})
       _folder=await utils.loadMetaParam(getUrlVars()["account"],"folder")
       //document.getElementById("personal_description").textContent=cleanText(desc.replaceAll("<br>","\n"))
        }catch(e){}
      this.setState({folder:_folder,meta:await utils.metadata(getUrlVars()["account"],getUrlVars()["account"])})
       
      await this.loadBlockchainData()
      try{
       document.getElementById("Avatar_face_container").innerHTML=""
       let avat = document.createElement('img')
       avat.setAttribute('src',utils.uri2url(await utils.loadMetaParam(getUrlVars()["account"],"image")))
        avat.setAttribute('class',"avatar_banner")
        document.getElementById("Avatar_face_container").appendChild(avat)
      }catch(e){}
       utils.appendBlocky(getUrlVars()["account"],40,document.getElementById("personalmenu"))
        this.loadSections(getUrlVars()["account"],"all")
        
    }

