import React, { Component } from 'react'
import './css.css';
import utils from '../../library'


class Ufactory extends Component {
    state={
        vault:"",
        standard:"item",
        counter:"0"
    }
     
    async setVault(_vault){
        await this.setState({vault:_vault})
        document.getElementById("factory_flyer").innerHTML=""
        document.getElementById("ufactory_mainlabel").textContent="DIR "
        this.loadFeed("default",'all',_vault,this.state.standard)
    }

    async setStandard(stn){
        await this.setState({standard:stn})
        document.getElementById("factory_flyer").innerHTML=""
        document.getElementById("ufactory_mainlabel").textContent="DIR "
        this.loadFeed("default",'all',this.state.vault,stn)
    }

    componentDidMount(){
        this.loadFeed("default",'all',this.state.vault)
       }
    
       async loadFeed(blck,who,vault,standard){
           let vaultx
        if(vault=="visual"){
        document.getElementById("ufactory_mainlabel").textContent=vault+""
        }
        if(vault=="vox"){
        document.getElementById("ufactory_mainlabel").textContent=vault+""
        }
  
        if(vault=="audio"){
        document.getElementById("ufactory_mainlabel").textContent=vault+""
        }
 
        if(vault=="video"){
        document.getElementById("ufactory_mainlabel").textContent=vault+""
        }
      
        let count=await utils.loadUnofficialEvents(blck,who,vault,standard,document.getElementById("factory_flyer"))
        this.setState({counter:count.length})
       }
    
    render(){
        console.log(this.props.update) 
        let count=""+this.state.counter
        return ( 
        <>
        <div id="ufactory_header">
        {(this.state.vault!="")?
        <>
        <div id="ufactory_backbutton" onClick={() => this.setVault("")}>back</div>
        <div className={(this.state.standard=="item")?"ufactory_setstandard_button dark darkblue text white":"ufactory_setstandard_button text white"} onClick={() => this.setStandard("item")}>ITEMS</div>
        <div className={(this.state.standard=="erc721")?"ufactory_setstandard_button dark darkblue text white":"ufactory_setstandard_button text white"} onClick={() => this.setStandard("erc721")}>ERC721</div>
        </>
        :
        <></>
        }
        <div id="ufactory_mainlabel" className="text white">DIR </div>
        {(this.state.vault!="")?<div id="ufactory_maincounter" className="dark darkblue text white">{count}</div>:<></>}
        
        </div>
        <div className="clear"/>
        {(this.state.vault!="")?
        <>
        </>
        :
        <>
        <div className="ufactory_section_button dark darkblue" onClick={() => this.setVault("visual")}> VISUAL</div>
        <div className="ufactory_section_button dark darkblue" onClick={() => this.setVault("vox")}> VOX</div>
        <div className="ufactory_section_button dark darkblue" onClick={() => this.setVault("audio")}> AUDIO</div>
        <div className="ufactory_section_button dark darkblue" onClick={() => this.setVault("video")}> VIDEO</div>
        </>
        }
        <div id="factory_flyer">     
        </div>
        </>
        );
    }


}




export default Ufactory